@use 'variables' as v; 
@use 'mixins' as m;

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
}
body, button, input, textarea {
    font-family: 'Poppins', sans-serif;
    font-size: 12px;
}
body {
    background-color: v.$color-pirmario;
    color: #3FC8E5;
    line-height: 1.7;
    font-weight: 400;
    transition: background 0.2s linear;
}
img {
    width: 100%;
    height: auto;
    vertical-align: middle;
}
ul {
    list-style: none;
}

.container {
    max-width: 1000px;
    margin: auto;
}
.row {
    display: flex;
    flex-wrap: wrap; /* vertical */
}
.section .container {
    margin-top: 3.75rem;
    margin-bottom: 4.75rem;
}

.section-title {
    font-size: 2.25rem;
    margin-left: 0.75rem;
    margin-bottom: 2.5rem;
    position: relative;

    &::before {
        position: absolute;
        content: '';
        top: 55px;
        bottom: 0;
        width: 4rem;
        display: block;
        height: .5rem;
        background-color: v.$color-heading;
    }
}
.padd-15 {
    padding-left: 15px;
    padding-right: 15px;
}
.degraded {
    color: transparent;
    background: linear-gradient(to right, v.$color-secundario 0%, v.$color-heading 100%);
    text-transform: uppercase;
    font-size: 2rem;
    background-clip: text;

    @include m.tablet {
        font-size: 1.5rem;
    }
}
.sticky {
    position: sticky;
    top: 80px;
    text-transform: capitalize;

    @include m.tablet {
        position: unset;
    }
}


