@use 'base/variables' as v;
@use 'base/mixins' as m;

// CHECK HEADER PARA EL LIGHT THEME
.checkbox {
    opacity: 0;
    position: absolute;
    transition: .3s;
}

.label {
    background-color: black;
    opacity: .7;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px;
    border-radius: 50px;
    height: 26px;
    width: 50px;
    position: relative;
    transform: scale(1.2);
}

.nav-menu .nav-list .nav-item .label .fa-moon,
.nav-menu .nav-list .nav-item .label .fa-sun  {
    color: yellow;
}
.ball {
    border-radius: 50%;
    top: 2px;
    left: 2px;
    background-color: white;
    position: absolute;
    width: 22px;
    height: 22px;
    transition: .3s;
}
.checkbox:checked + .label .ball {
    transform: translateX(24px);
}


// BOTONES
.btn {
    padding: 0.75rem 2rem;
    line-height: 1;
    border-radius: 5px;
}
.btn-default {
    background-color: rebeccapurple;
    color: white;
    vertical-align: middle;
    display: inline-block;
    border: 1px solid rebeccapurple;
    transition: .3s;

    &:hover {
        background-color: transparent;
        border: 1px solid rebeccapurple;
    }

}

// PARTICLES JS
#particles-js{ 
    position:fixed; 
    width: 100%; 
    height: 100%; 
    z-index: -1;
} 


// Greeting
.greeting {
    font-size: 25px;
    animation-name: greeting-animation; // Refers to the name of your @keyframes element below
    animation-duration: 2s; // Change to speed up or slow down
    animation-iteration-count: infinite; // Never stop waving :)
    transform-origin: 70% 70%; // Pivot around the bottom-left palm
    display: inline-block;
}
  
@keyframes greeting-animation {
    0% {
        transform: rotate(0deg);
    }
    10% {
        transform: rotate(14deg);
    } // The following five values can be played with to make the waving more or less extreme
    20% {
        transform: rotate(-8deg);
    }
    30% {
        transform: rotate(14deg);
    }
    40% {
        transform: rotate(-4deg);
    }
    50% {
        transform: rotate(10deg);
    }
    60% {
        transform: rotate(0deg);
    } /* Reset for the last half to pause */
    100% {
        transform: rotate(0deg);
    }
}