@use 'base/variables' as v;
@use 'base/mixins' as m;

.about-content {
    flex: 0 0 100%;
    max-width: 100%;
}
.about-content .about-text,
.about-content .skills {
    flex: 0 0 50%;
    max-width: 50%;

    @include m.tablet {
        flex: 0 0 100%;
        max-width: 100%;
    }
}
.rounded {
    background-color: v.$color-secundario;
    padding: 1.9rem 0.95rem;
    border-radius: 5px;
}
.about-text p {
    text-align: justify;
    margin-bottom: 1rem;
}
.about-content .skills .skill-item {
    flex: 0 0 100%;
    max-width: 100%;

    h4 {
        color: v.$color-heading;
        margin-bottom: .5rem;
    }

    img {
        width: 40px;
        height: 40px;
        margin-bottom: .5rem;
        margin-right: .5rem;
        transition: .3s;

        &:hover {
            transform: scale(2.5);
        }
    }
   
}

