@use 'base/variables' as v;
@use 'base/mixins' as m;

body.dark {
    background-color: #eaf2e3;
    color: black;
}
body.dark .social-icons i {
    color: #3e3e47;
}
body.dark .contact-social-icons i {
    color: #3e3e47;
}
body.dark h1 {
    color: v.$color-heading;
}
body.dark h2 {
    color: v.$color-heading;
}
body.dark h3 {
    color: v.$color-heading;
}
body.dark a{
    color: #3e3e47;
}
body.dark a:hover{
    color: v.$color-heading;
}
body.dark .header {
   
    /*background-color: v.$color-secundario;*/
    background: linear-gradient(135deg, rgba(255,255,255,0.1), rgba(255,255,255,0));
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    border-bottom: 1px solid rgba(255,255,255,0.18);
    box-shadow: 0 0 30px 0 rgba(0,0,0,0.37);
    
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
}
@media (max-width: 768px) {
    body.dark .nav-menu {
        background-color: white;
    }
}
body.dark .degraded {
    color: transparent;
    background: linear-gradient(to right, v.$color-secundario 0%, v.$color-heading 100%);
    text-transform: uppercase;
    font-size: 3.5rem;
    background-clip: text;
    -webkit-background-clip: text;
}
@media (max-width: 768px) {
    body.dark .degraded {
        font-size: 1.5rem;
    }   
}
body.dark .rounded {
    background-color: white;
}
body.dark .about-content .skills .skill-item img:hover {
    background-color: transparent;
}
body.dark .btn-default {
    background-color: rebeccapurple;
    color: white;
    border: 1px solid rebeccapurple;
}
body.dark .btn-default:hover {
    color: #3e3e47;
    background-color: transparent;
    border: 1px solid rebeccapurple;
}
body.dark .timeline-box{
    background-color: white;
}
body.dark .timeline{
    background-color: white;
}
body.dark .timeline-item{
    background-color: white;
}
body.dark .timeline-icon{
    background-color: white;
}
body.dark .portfolio-item{
    background-color: white;
}
body.dark .contact-input {
    color: #3e3e47;
}
body.dark .fa-solid {
    color: #3e3e47;
}

body.dark .contact-description .fa-whatsapp , 
body.dark .contact-description .fa-envelope {
    color: #3e3e47 !important;
}