@use 'base/variables' as v;
@use 'base/mixins' as m;


.education {
    flex: 0 0 50%;
    max-width: 50%;
    margin-top: 0;

    @include m.tablet {
        flex: 0 0 100%;
        max-width: 100%;
        margin-top: 10px;
    }
}
.experience {
    flex: 0 0 50%;
    max-width: 50%;

    @include m.tablet {
        flex: 0 0 100%;
        max-width: 100%;
    }
}
.timeline-box {
    flex: 0 0 100%;
    max-width: 100%;
}
.timeline {
    background-color: v.$color-secundario;
    padding: 1.9rem 0.95rem;
    border-radius: 5px;
    width: 100%;
    position: relative;

    &-item {
        position: relative;
        padding-left: 2.5rem;
        padding-bottom: 1.9rem;

        &:last-child {
            padding-bottom: 0;
        }

        &::before {
            content: '';
            width: 1px;
            position: absolute;
            height: 100%;
            left: 0.5rem;
            background-color: rebeccapurple;
            top: 0;
        }
    }

    &-icon {
        position: absolute;
        left: -0.25rem;
        top: 0;
        font-size: 1.5rem;
        background-color: v.$color-secundario;
        color: rebeccapurple;
    }
    &-date {
        font-size: 1.2rem;
        color: gray;

        .fa-calendar-days {
            color: gray;
        }
        .timeline-freelance {
            color: rebeccapurple;
        }
        .fa-solid { 
            margin-right: 0.3rem;
        }
    }

    &-title {
        margin: 0.75rem 0;
    }
}

