@use 'base/variables' as v;
@use 'base/mixins' as m;

.portfolio {
    flex: 0 0 100%;
    max-width: 100%;
    
    &-box {
        flex: 0 0 100%;
        max-width: 100%;
    }

    &-item {
        margin-left: auto;
        width: 80%;
        height: auto;
        display: flex;
        border-radius: 5px;
        background-color: v.$color-secundario;
        margin-bottom: 1rem;
        overflow: hidden;   
        
        @include m.tablet {
            width: 100%;
            flex-direction: column;
        }

        &:nth-child(even) {
            flex-direction: row-reverse;
            
            @include m.tablet {
                flex-direction: column;
            }
        }  
    }

    &-img {
        flex: 0 0 50%;
        height: auto;
        
        @include m.tablet {
            width: 100%;
            padding: 1rem;
        }

    }

    &-content {
        flex: 0 0 50%;
        padding: 1rem;
    }

    &-tecnology { 
        font-weight: 500;
        font-size: .7rem;
        background-color: rebeccapurple;
        color: white;
        padding: .2rem;
        transition: .3s;
        
        &:hover {
            background-color: white;
            color: rebeccapurple;
        }
    }

    &-text { 
        text-align: left;
        line-height: 2;
        font-size: .7rem;
        margin-bottom: 1rem;
    }

    
}