@use 'base/variables' as v;
@use 'base/mixins' as m;


.contact-box {
    flex: 0 0 30%;
    max-width: 30%;
    margin-bottom: 0;

    @include m.tablet {
        flex: 0 0 100%;
        max-width: 100%;
        margin-bottom: 1rem;
    }
}
.contact-form {
    flex: 0 0 70%;
    max-width: 70%;
    margin: auto;
    
    @include m.tablet {
        flex: 0 0 80%;
        max-width: 80%;
        margin-bottom: 1rem;
    }

}
.contact-data {
    display: grid;
    row-gap: .5rem;

    @include m.tablet {
        row-gap: 1rem;
        text-align: center;
        align-items: center;
        justify-content: center;
    }
}
.contact-subtitle {
    margin-bottom: 0.5rem;
}
.contact-description { 
    display: inline-flex;
    align-items: center;
    column-gap: .5rem;

    i {
        display: inline-block;
        padding: .2rem;
        font-size: 2rem;
        color: white;
    }
}
.contact-social-icons li {
    display: inline-block;
    padding: .2rem;
    font-size: 2rem;
}
.contact-inputs {
    display: grid;
    row-gap: 2rem;
    margin-bottom: 2.5rem;
}
.contact-content {
    position: relative;
    height: 4rem;
    border-bottom: 2px solid grey;
}
.contact-content span {
    position: absolute;
    bottom: -2px;
    left: 0;
    content: '';
    height: 2px;
    width: 0;
    background: v.$color-heading;
    transition: 5s;
}
.contact-input:focus ~ span {
    width: 100%;
}
.contact-input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 2rem 1rem 1rem 0;
    background: none;
    color: white;
    outline: none;
    border: none;
    z-index: 1;
}
.contact-label {
    position: absolute;
    top: .40rem;
    width: 100%;
    font-size: .7rem;
    transition: .3s;
}
.contact-area {
    height: 7rem;
}
.contact-area textarea{
    resize: none;
}
.contact-input:focus + .contact-label {
    top: -.75rem;
    left: 0;
    z-index: 10;    
    color: v.$color-heading;
}
