@use 'base/variables' as v;
@use 'base/mixins' as m;


.home {
    display: flex;
    justify-content: center;
    height: 100vh;
    min-height: 100vh;

    .intro {
        max-width: 540px;
        text-align: center;

        &-avatar {
            width: 150px;
            height: 150px;
            border-radius: 50%;
        }

        img {
            margin: auto;
            margin-bottom: 1rem;
        }
        
    }

    .social-icons {
        margin-top: .8rem;

        li {
            display: inline-block;
            &:not(:last-child) {
                margin-right: 1.5rem;
            }
        }
        
        a {
            font-size: 1.5rem;
        }
    }

}

.scroll-down {
    position: absolute;
    bottom: 2rem;
    left: 0;
    width: 100%;
}
.mouse-wrapper {
    font-size: 1.2rem;
    display: block;
    margin: auto;
    max-width: 120px;
    text-align: center;
}
.mouse {
    border: 2px solid v.$color-heading;
    display: block;
    height: 1.75rem;
    width: 1.25rem;
    margin: auto;
    border-radius: 1rem;
    position: relative;

    .wheel {
        background-color: v.$color-heading;
        border-radius: 1rem;
        width: 0.25rem;
        height: 0.25rem;
        position: absolute;
        top: 0.5rem;
        left: 40%;
        display: block;
        animation: wheel 2s linear infinite;
    }
}
@keyframes wheel {
    0% {
        top: 29%;
    }
    15% {
        top: 50%;
    }
    50% {
        top: 50%;
    }
    100% {
        top: 29%;
    }
}
