@use 'base/variables' as v;
@use 'base/mixins' as m;

header {
    width: 100%;
    // background-color: var(--color-secundario);
    background: linear-gradient(135deg, rgba(255,255,255,0.1), rgba(255,255,255,0));
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    border-bottom: 1px solid rgba(255,255,255,0.18);
    box-shadow: 0 0 30px 0 rgba(0,0,0,0.37);

    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
}

.logo {
    display: none;

    @include m.tablet {
        width: 100%;
        display: inline-block;
        text-align: center;
    }
}


.nav {
    height: calc(2.5rem + 1.5rem);
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 3rem;


    &-menu {
        margin: auto;
    }
    @include m.tablet {
        &-menu {
            position: fixed;
            background: v.$color-secundario;
            max-width: 100%;
            width: 100%;
            height: 100vh;
            top: 0;
            right: -100%;
            padding: 4rem 4rem 0 4rem;
            transition: .3s;
            z-index: 100;
        }
    }

    &-list {
        display: flex;
        column-gap: 2rem;
    }
    @include m.tablet {
        &-list {
            flex-direction: column;
            justify-content: center;
            align-items: center;
            row-gap: 1rem;
        }
    }

    &-link {
        font-size: 1.1rem;
        font-weight: 900;
        transition: .3s;

        &:hover {
            color: grey;
        }
    }
    @include m.tablet {
        &-link {
            font-size: 2rem;
        }
        .show-menu {
            right: 0;
        }
    }
    

}

.active-link {
    position: relative;

    &::before {
        position: absolute;
        content: '';
        bottom: -0.55rem;
        left: 0;
        display: inline-block;
        width: 100%;
        height: .3rem;
        background-color: rebeccapurple;
    }
}

.nav-close {
    font-size: 3.2rem;
    position: absolute;
    color: red;
    top: -10px;
    right: 1.25rem;
    cursor: pointer;
}
.nav-toggle {
    font-size: 3.2rem;
    color: yellow;
    cursor: pointer;
    margin-left: auto;
}
.nav-close,
.nav-toggle {
    display: none;
   
    @include m.tablet {
        display: inline-block;
    }

}
