* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body, button, input, textarea {
  font-family: "Poppins", sans-serif;
  font-size: 12px;
}

body {
  background-color: #150029;
  color: #3FC8E5;
  line-height: 1.7;
  font-weight: 400;
  transition: background 0.2s linear;
}

img {
  width: 100%;
  height: auto;
  vertical-align: middle;
}

ul {
  list-style: none;
}

.container {
  max-width: 1000px;
  margin: auto;
}

.row {
  display: flex;
  flex-wrap: wrap;
  /* vertical */
}

.section .container {
  margin-top: 3.75rem;
  margin-bottom: 4.75rem;
}

.section-title {
  font-size: 2.25rem;
  margin-left: 0.75rem;
  margin-bottom: 2.5rem;
  position: relative;
}
.section-title::before {
  position: absolute;
  content: "";
  top: 55px;
  bottom: 0;
  width: 4rem;
  display: block;
  height: 0.5rem;
  background-color: #D63EE5;
}

.padd-15 {
  padding-left: 15px;
  padding-right: 15px;
}

.degraded {
  color: transparent;
  background: linear-gradient(to right, #2B034B 0%, #D63EE5 100%);
  text-transform: uppercase;
  font-size: 2rem;
  background-clip: text;
}
@media (max-width: 768px) {
  .degraded {
    font-size: 1.5rem;
  }
}

.sticky {
  position: sticky;
  top: 80px;
  text-transform: capitalize;
}
@media (max-width: 768px) {
  .sticky {
    position: unset;
  }
}

h1, h2, h3 {
  margin: 0;
  font-weight: 600;
  color: #D63EE5;
}

h1 {
  font-size: 2.25rem;
}

h2 {
  font-size: 1.75rem;
}

h3 {
  font-size: 1.25rem;
}

/*
[class$="heading"] {
    margin: 2rem 0;
} */
a {
  text-decoration: none;
  color: white;
}

/*
.btn {
    @include m.boton(v.$blanco, v.$negro);
}
.boton-negro {
    @include m.boton(v.$negro, v.$blanco);
}
*/
.checkbox {
  opacity: 0;
  position: absolute;
  transition: 0.3s;
}

.label {
  background-color: black;
  opacity: 0.7;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5px;
  border-radius: 50px;
  height: 26px;
  width: 50px;
  position: relative;
  transform: scale(1.2);
}

.nav-menu .nav-list .nav-item .label .fa-moon,
.nav-menu .nav-list .nav-item .label .fa-sun {
  color: yellow;
}

.ball {
  border-radius: 50%;
  top: 2px;
  left: 2px;
  background-color: white;
  position: absolute;
  width: 22px;
  height: 22px;
  transition: 0.3s;
}

.checkbox:checked + .label .ball {
  transform: translateX(24px);
}

.btn {
  padding: 0.75rem 2rem;
  line-height: 1;
  border-radius: 5px;
}

.btn-default {
  background-color: rebeccapurple;
  color: white;
  vertical-align: middle;
  display: inline-block;
  border: 1px solid rebeccapurple;
  transition: 0.3s;
}
.btn-default:hover {
  background-color: transparent;
  border: 1px solid rebeccapurple;
}

#particles-js {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.greeting {
  font-size: 25px;
  animation-name: greeting-animation;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  transform-origin: 70% 70%;
  display: inline-block;
}

@keyframes greeting-animation {
  0% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(14deg);
  }
  20% {
    transform: rotate(-8deg);
  }
  30% {
    transform: rotate(14deg);
  }
  40% {
    transform: rotate(-4deg);
  }
  50% {
    transform: rotate(10deg);
  }
  60% {
    transform: rotate(0deg);
  }
  /* Reset for the last half to pause */
  100% {
    transform: rotate(0deg);
  }
}
body.dark {
  background-color: #eaf2e3;
  color: black;
}

body.dark .social-icons i {
  color: #3e3e47;
}

body.dark .contact-social-icons i {
  color: #3e3e47;
}

body.dark h1 {
  color: #D63EE5;
}

body.dark h2 {
  color: #D63EE5;
}

body.dark h3 {
  color: #D63EE5;
}

body.dark a {
  color: #3e3e47;
}

body.dark a:hover {
  color: #D63EE5;
}

body.dark .header {
  /*background-color: v.$color-secundario;*/
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0));
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border-bottom: 1px solid rgba(255, 255, 255, 0.18);
  box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.37);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
}

@media (max-width: 768px) {
  body.dark .nav-menu {
    background-color: white;
  }
}
body.dark .degraded {
  color: transparent;
  background: linear-gradient(to right, #2B034B 0%, #D63EE5 100%);
  text-transform: uppercase;
  font-size: 3.5rem;
  background-clip: text;
  -webkit-background-clip: text;
}

@media (max-width: 768px) {
  body.dark .degraded {
    font-size: 1.5rem;
  }
}
body.dark .rounded {
  background-color: white;
}

body.dark .about-content .skills .skill-item img:hover {
  background-color: transparent;
}

body.dark .btn-default {
  background-color: rebeccapurple;
  color: white;
  border: 1px solid rebeccapurple;
}

body.dark .btn-default:hover {
