  background-color: transparent;
  border: 1px solid rebeccapurple;
}

body.dark .timeline-box {
  background-color: white;
}

body.dark .timeline {
  background-color: white;
}

body.dark .timeline-item {
  background-color: white;
}

body.dark .timeline-icon {
  background-color: white;
}

body.dark .portfolio-item {
  background-color: white;
}

body.dark .contact-input {
  color: #3e3e47;
}

body.dark .fa-solid {
  color: #3e3e47;
}

body.dark .contact-description .fa-whatsapp,
body.dark .contact-description .fa-envelope {
  color: #3e3e47 !important;
}

header {
  width: 100%;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0));
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border-bottom: 1px solid rgba(255, 255, 255, 0.18);
  box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.37);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
}

.logo {
  display: none;
}
@media (max-width: 768px) {
  .logo {
    width: 100%;
    display: inline-block;
    text-align: center;
  }
}

.nav {
  height: 4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 3rem;
}
.nav-menu {
  margin: auto;
}
@media (max-width: 768px) {
  .nav-menu {
    position: fixed;
    background: #2B034B;
    max-width: 100%;
    width: 100%;
    height: 100vh;
    top: 0;
    right: -100%;
    padding: 4rem 4rem 0 4rem;
    transition: 0.3s;
    z-index: 100;
  }
}
.nav-list {
  display: flex;
  column-gap: 2rem;
}
@media (max-width: 768px) {
  .nav-list {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    row-gap: 1rem;
  }
}
.nav-link {
  font-size: 1.1rem;
  font-weight: 900;
  transition: 0.3s;
}
.nav-link:hover {
  color: grey;
}
@media (max-width: 768px) {
  .nav-link {
    font-size: 2rem;
  }
  .nav .show-menu {
    right: 0;
  }
}

.active-link {
  position: relative;
}
.active-link::before {
  position: absolute;
  content: "";
  bottom: -0.55rem;
  left: 0;
  display: inline-block;
  width: 100%;
  height: 0.3rem;
  background-color: rebeccapurple;
}

.nav-close {
  font-size: 3.2rem;
  position: absolute;
  color: red;
  top: -10px;
  right: 1.25rem;
  cursor: pointer;
}

.nav-toggle {
  font-size: 3.2rem;
  color: yellow;
  cursor: pointer;
  margin-left: auto;
}

.nav-close,
.nav-toggle {
  display: none;
}
@media (max-width: 768px) {
  .nav-close,
.nav-toggle {
    display: inline-block;
  }
}

.home {
  display: flex;
  justify-content: center;
  height: 100vh;
  min-height: 100vh;
}
.home .intro {
  max-width: 540px;
  text-align: center;
}
.home .intro-avatar {
  width: 150px;
  height: 150px;
  border-radius: 50%;
}
.home .intro img {
  margin: auto;
  margin-bottom: 1rem;
}
.home .social-icons {
  margin-top: 0.8rem;
}
.home .social-icons li {
  display: inline-block;
}
.home .social-icons li:not(:last-child) {
  margin-right: 1.5rem;
}
.home .social-icons a {
  font-size: 1.5rem;
}

.scroll-down {
  position: absolute;
  bottom: 2rem;
  left: 0;
  width: 100%;
}

.mouse-wrapper {
  font-size: 1.2rem;
  display: block;
  margin: auto;
  max-width: 120px;
  text-align: center;
}

.mouse {
  border: 2px solid #D63EE5;
  display: block;
  height: 1.75rem;
  width: 1.25rem;
  margin: auto;
  border-radius: 1rem;
  position: relative;
}
.mouse .wheel {
  background-color: #D63EE5;
  border-radius: 1rem;
  width: 0.25rem;
  height: 0.25rem;
  position: absolute;
  top: 0.5rem;
  left: 40%;
  display: block;
  animation: wheel 2s linear infinite;
}

@keyframes wheel {
  0% {
    top: 29%;
  }
  15% {
    top: 50%;
  }
  50% {
    top: 50%;
  }
  100% {
    top: 29%;
  }
}
.about-content {
  flex: 0 0 100%;
  max-width: 100%;
}

.about-content .about-text,
.about-content .skills {
  flex: 0 0 50%;
  max-width: 50%;
}
@media (max-width: 768px) {
  .about-content .about-text,
.about-content .skills {
    flex: 0 0 100%;
    max-width: 100%;
  }
}

.rounded {
  background-color: #2B034B;
  padding: 1.9rem 0.95rem;
  border-radius: 5px;
}

.about-text p {
  text-align: justify;
  margin-bottom: 1rem;
}

.about-content .skills .skill-item {
  flex: 0 0 100%;
  max-width: 100%;
}
.about-content .skills .skill-item h4 {
  color: #D63EE5;
  margin-bottom: 0.5rem;
}
.about-content .skills .skill-item img {
  width: 40px;
  height: 40px;
  margin-bottom: 0.5rem;
  margin-right: 0.5rem;
  transition: 0.3s;
}
.about-content .skills .skill-item img:hover {
  transform: scale(2.5);
}

.education {
  flex: 0 0 50%;
  max-width: 50%;
  margin-top: 0;
}
@media (max-width: 768px) {
  .education {
    flex: 0 0 100%;
    max-width: 100%;
    margin-top: 10px;
  }
}

.experience {
  flex: 0 0 50%;
  max-width: 50%;
}
@media (max-width: 768px) {
  .experience {
    flex: 0 0 100%;
    max-width: 100%;
  }
}

.timeline-box {
  flex: 0 0 100%;
  max-width: 100%;
}

.timeline {
  background-color: #2B034B;
  padding: 1.9rem 0.95rem;
  border-radius: 5px;
  width: 100%;
  position: relative;
}
.timeline-item {
  position: relative;
  padding-left: 2.5rem;
  padding-bottom: 1.9rem;
}
.timeline-item:last-child {
  padding-bottom: 0;
}
.timeline-item::before {
  content: "";
  width: 1px;
  position: absolute;
  height: 100%;
  left: 0.5rem;
  background-color: rebeccapurple;
  top: 0;
}
.timeline-icon {
  position: absolute;
  left: -0.25rem;
  top: 0;
  font-size: 1.5rem;
  background-color: #2B034B;
  color: rebeccapurple;
}
.timeline-date {
  font-size: 1.2rem;
  color: gray;
}
.timeline-date .fa-calendar-days {
  color: gray;
}
.timeline-date .timeline-freelance {
  color: rebeccapurple;
}
.timeline-date .fa-solid {
  margin-right: 0.3rem;
}
.timeline-title {
  margin: 0.75rem 0;
}

.portfolio {
  flex: 0 0 100%;
  max-width: 100%;
}
.portfolio-box {
  flex: 0 0 100%;
  max-width: 100%;
}
.portfolio-item {
  margin-left: auto;
  width: 80%;
  height: auto;
  display: flex;
  border-radius: 5px;
  background-color: #2B034B;
  margin-bottom: 1rem;
  overflow: hidden;
}
@media (max-width: 768px) {
  .portfolio-item {
    width: 100%;
    flex-direction: column;
  }
}
.portfolio-item:nth-child(even) {
  flex-direction: row-reverse;
}
@media (max-width: 768px) {
  .portfolio-item:nth-child(even) {
    flex-direction: column;
  }
}
.portfolio-img {
  flex: 0 0 50%;
  height: auto;
}
@media (max-width: 768px) {
  .portfolio-img {
    width: 100%;
    padding: 1rem;
  }
}
.portfolio-content {
  flex: 0 0 50%;
  padding: 1rem;
}
.portfolio-tecnology {
  font-weight: 500;
  font-size: 0.7rem;
  background-color: rebeccapurple;
  color: white;
  padding: 0.2rem;
  transition: 0.3s;
}
.portfolio-tecnology:hover {
  background-color: white;
  color: rebeccapurple;
}
.portfolio-text {
  text-align: left;
  line-height: 2;
  font-size: 0.7rem;
  margin-bottom: 1rem;
}

.contact-box {
  flex: 0 0 30%;
  max-width: 30%;
  margin-bottom: 0;
}
@media (max-width: 768px) {
  .contact-box {
    flex: 0 0 100%;
    max-width: 100%;
    margin-bottom: 1rem;
  }
}

.contact-form {
  flex: 0 0 70%;
  max-width: 70%;
  margin: auto;
}
@media (max-width: 768px) {
  .contact-form {
    flex: 0 0 80%;
    max-width: 80%;
    margin-bottom: 1rem;
  }
}

.contact-data {
  display: grid;
  row-gap: 0.5rem;
}
@media (max-width: 768px) {
  .contact-data {
    row-gap: 1rem;
    text-align: center;
    align-items: center;
    justify-content: center;
  }
}

.contact-subtitle {
  margin-bottom: 0.5rem;
}

.contact-description {
  display: inline-flex;
  align-items: center;
  column-gap: 0.5rem;
}
.contact-description i {
  display: inline-block;
  padding: 0.2rem;
  font-size: 2rem;
  color: white;
}

.contact-social-icons li {
  display: inline-block;
  padding: 0.2rem;
  font-size: 2rem;
}

.contact-inputs {
  display: grid;
  row-gap: 2rem;
  margin-bottom: 2.5rem;
}

.contact-content {
  position: relative;
  height: 4rem;
  border-bottom: 2px solid grey;
}

.contact-content span {
  position: absolute;
  bottom: -2px;
  left: 0;
  content: "";
  height: 2px;
  width: 0;
  background: #D63EE5;
  transition: 5s;
}

.contact-input:focus ~ span {
  width: 100%;
}

.contact-input {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 2rem 1rem 1rem 0;
  background: none;
  color: white;
  outline: none;
  border: none;
  z-index: 1;
}

.contact-label {
  position: absolute;
  top: 0.4rem;
  width: 100%;
  font-size: 0.7rem;
  transition: 0.3s;
}

.contact-area {
  height: 7rem;
}

.contact-area textarea {
  resize: none;
}

.contact-input:focus + .contact-label {
  top: -0.75rem;
  left: 0;
  z-index: 10;
  color: #D63EE5;
}

.footer {
  padding: 60px 0;
}

.footer-text {
  text-align: center;
}